import './main.css';
import { Elm } from './Picshare.elm'; //import the Elm application from ./Picshare.elm
import * as serviceWorker from './serviceWorker';
//applications main entry point
//imports CSS from src/main.css and the Elm applicaiton as Elm from src/Main.elm
//also imports a serviceWorker module for registering and unregistering a ServiceWorker
//initialises the Elm application inside a div element with an id of root
//Create Elm App's underlying Webpack configuration allows Webpack to detect
//when you import non-JavaScript files and do special work with them. For
//example, importing CSS will cause Webpack to generate a style tag during
//local development and an actual stylesheet for production deployment
//More importantly, importing Elm will make Webpack use the Elm tooling to
//compile the Elm file and return a compiled JavaScript module.The compiled
//JavaScript module includes the init() method, which Create Elm App uses to
//embed the Elm application in the DOM.The actual DOM layout comes from
//public / index.html, which Create Elm App will serve to the browser.
// Elm.Main.init({
//   node: document.getElementById('root')
// });
var app = Elm.Picshare.init({ //init function for every compiled module that returns an application object
  node: document.getElementById('root') //display Elm in this node (deploy app.)
});
app.ports.listen.subscribe(listen); //port subscription.JS subscribed to the outgoing port "listen"
function listen(url) { //receive url from port
  var socket = new WebSocket(url); //create new instance of WebSocket with it
  //"Websocket" = built-in JS constructor function for connecting to WebSocket servers
  socket.onmessage = function (event) { //define WebSocket's onmessage callback (receives events from server)
    app.ports.receive.send(event.data); //pull photo from event."data"(property) and pass it into function
  }; //ports that receive data such as "receive" provide a "send" method for JS to forward data to Elm
}
//If you want your app to work offline and load faster, you can change
//unregister() to register() below. Note this comes with some pitfalls.
//Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
//Essentially, you change the Elm.Main.init call to Elm.Picshare.init, accept the app (down)
//return value like you did before, and wire up the ports for WebSocket communication
